<template>
    <div class="wrapper-control">
        <!-- <div> -->

            <button class="control btn-new" v-on:click="newGame">
            <ion-icon name="add-circle-outline"></ion-icon>
            New Game
        </button>

        <button class="control btn-roll" v-on:click="roll">
            <ion-icon name="reload-outline"></ion-icon>
            Roll Dice
        </button>

        <button class="control btn-hold" 
            v-on:click="this.$emit('handleHold')"
        >
            <ion-icon name="download-outline"></ion-icon>
            Hold
        </button>

        <input
            v-bind:disabled="isPlaying"
            v-bind:value="finalScore"
            v-on:input="$emit('changeFinalScore', $event.target.value)"
            type="number" placeholder="Final score" class="final-score">
        <!-- <button v-on:click="console.log(isPlaying)">check playing</button> -->
        
        <!-- </div> -->
    </div>
</template>

<script>
export default {
    name: "CompControls",
    props: {
        finalScore : {type : [Number, String], default: () => 10},
        isPlaying : {type: Boolean, default: () => false}
    },
    data() {
        return {

        }
    },
    methods: {
        newGame() {
            console.log("newGame Controls.vue")
            this.$emit("handleNewGame")
        },
        roll() {
            console.log("roll Controls.vue")
            this.$emit('handleRoll')
        },
        
    }
}
</script>

<style>
.control {
    position: absolute;
    left: 50%;
    transform : translate(-50%);
    border: none;
    font-size: 20px;
    width: 200px;
    color: #555;
    cursor:pointer;
    background: none;
    transition: background-color 0.3s, color 0.3s;
}

.control.disable {
    pointer-events: none;
}

.control:hover {
    font-weight: 600;
}
.control:hover ion-icon {
    margin-right: 10px;
}

.btn-new {
    top: 45px;
}

.btn-roll {
    top: 403px;
}

.btn-hold {
    top: 467px;
}

.control ion-icon {
    margin: 0 10px;
    font-size: 24px;
    color: rgb(48, 145, 53); 
    vertical-align: text-top;
}

.final-score {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 520px;
    padding: 10px;
    width: 160px;
    text-transform: uppercase;
    text-align: center;
}

.final-score:focus { outline: none; }

</style>