<template>
    <div id="app">
      <div class="wrapper clearfix">
        <players
          v-bind:scorePlayer="scorePlayer"
          v-bind:activePlayer="activePlayer"
          v-bind:currentScore="currentScore"
        />
        <controls
          v-on:handleNewGame="handleNewGame"
          v-on:handleRoll="handelRoll"
          v-on:handleHold="handleHold"
          v-on:changeFinalScore="changeFinalScore"
          v-bind:finalScore="finalScore"
          v-bind:isPlaying="isPlaying"
        />
        <dices
          v-bind:dices="dices"
        />
        <popup-rule
          v-on:handlePopup = "handlePopup"
          v-bind:isOpenPopup="isOpenPopup"
        />
      </div>
    </div>
</template>

<script>
import Players from "./components/Players.vue"
import Controls from "./components/Controls.vue"
import Dices from "./components/Dices.vue"
import PopupRule from "./components/PopupRule.vue"
export default {
  name: 'App',
  data() {
    return {
      isPlaying: false,
      isOpenPopup: false,
      activePlayer: 0,
      scorePlayer: [10, 23],
      currentScore: 20,
      dices : [2, 2],
      finalScore : 10
    }
  },
  components: {
    Players,
    Controls,
    Dices,
    PopupRule,
},
  methods: {
    handleNewGame() {
      console.log("handleNewGame App.vue")
      this.isOpenPopup = true
    },
    handlePopup() {
      console.log("handlePopUp App.vue")
      this.isPlaying = true
      this.isOpenPopup = false
      this.activePlayer = 1,
      this.scorePlayer = [0, 0]
      this.currentScore = 0
      this.dices = [1, 1]
      
    },
    nextPLayer() {
      console.log("nextPLayer App.vue")
      this.activePlayer = this.activePlayer === 0 ? 1: 0
      this.currentScore = 0
      
    },  
    handelRoll() {
      if (this.isPlaying) {
        console.log("handelRoll App.vue")
        var dice1 = Math.floor(Math.random() * 6) +1
        var dice2 = Math.floor(Math.random() * 6) +1
        this.dices = [dice1, dice2]
        if (dice1 === 1 || dice2  === 1) {
          setTimeout( () => {
            alert(`player ${this.activePlayer + 1} was spin at number one, MISS`)
          }, 10)
          this.nextPLayer()
        }else {
          this.currentScore = this.currentScore + dice1 + dice2
        }
      }
      else {
        alert("please press New Game..")
      } 
    },
    handleHold() {
      console.log("handleHold App.vue")
      if (this.isPlaying) {
        var {activePlayer, scorePlayer, currentScore} = this
        let scoreOld = scorePlayer[activePlayer] + currentScore
        scorePlayer[activePlayer] = scoreOld        
        this.nextPLayer()
      }else {
        alert("please press New Game..")
      }
    },
    changeFinalScore(score) {
      var newScore = parseInt(score)
      if (isNaN(newScore)) {
        this.finalScore = ""
      } else {
        this.finalScore = newScore
      }
    }
  }
}
</script>

