<template>
    <div class="wrapper-popup" v-bind:class="getClassPopup">
        <div class="rule">
            <h3>Luật chơi</h3>
            <p>
                Lorem ipsum dolor sit, amet consectetur adipisicing elit. Neque eos et asperiores saepe eum? Ipsum, debitis aliquam fugit doloribus est optio? Est tenetur nemo voluptatem a qui odit obcaecati nam.
            </p>
            <button
                v-on:click="confirm"
                class="confirm">Đã hiểu</button>
        </div>
    </div>
</template>

<script>
export default {
    name: "popup-rule",
    props: {
        isOpenPopup : { type: Boolean, default: () => false}
    },
    data() {
         return {

         }
    },
    methods: {
        confirm() {
            this.$emit('handlePopup')
        }
    },
    computed: {
        getClassPopup() {
            return {
                "open-popup": this.isOpenPopup
            }
        }
    }
}
</script>

<style>
.wrapper-popup {
    position: fixed;
    top:0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease;
}

.wrapper-popup.open-popup {
    opacity: 1;
    visibility: visible;
    transition: all .3s ease;
}

.rule {
    width: 500px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.2);
    padding: 20px;
    transition: all .3s ease;
    border-radius: 4px;
}

.open-popup .rule {
    transform: translate(-50%, -50%) scale(1.5);
    transition: all .3s ease;
}

.rule h3 {
    margin-bottom: 12px;
    text-align: center;
    text-transform: uppercase;
}

.rule p {
    text-align: justify;
}

.rule .confirm {
    margin-top: 12px;
    padding: 8px 12px;
    width: 100%;
}
.rule .confirm:hover {
    background-color: #555;
    color: #fff;
    cursor: pointer;
    transition: all .3s ease;
}
</style>